html,
body,
#root {
  height: 100%;
  width: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

img {
  max-width: 100%;
}

a {
  color: inherit;
}

.textreplace {
  display: block;
  padding: 0;
  text-indent: 100%;
  white-space: nowrap;
  overflow: hidden;
}

.textreplace-sr {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

.fullsize {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

/*https://gist.github.com/terkel/4373420#gistcomment-1626534*/
body {
  background: #292929;
}

.app {
  background: #292929;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  width: 100%;
  padding: 20px;
}
@media (min-width: 48rem) {
  .app {
    justify-content: center;
    height: 100%;
  }
}
@media (min-width: 48rem) {
  .app--opened {
    height: auto;
  }
}
.app__row {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  width: 100%;
  margin-bottom: 20px;
}
@media (min-width: 48rem) {
  .app__row {
    width: 100%;
    max-width: 700px;
    flex-direction: row;
  }
}
.app__column {
  margin-top: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  width: 100%;
}
@media (min-width: 48rem) {
  .app__column {
    width: 700px;
  }
}
.app__column--invoice-loaded {
  margin-top: 0;
}
.app__column--error {
  margin-top: 0;
}
.app__submit {
  display: flex;
  flex-direction: row;
  position: relative;
  margin-top: 20px;
}
@media (min-width: 48rem) {
  .app__submit {
    margin-top: 0;
    padding-left: 10px;
  }
}
.app__submit--invoice-loaded {
  position: absolute;
  margin-top: 0;
  right: 0;
  top: 7px;
}

.error {
  position: relative;
  width: 100%;
}
.error__container {
  align-items: center;
  background: #3e0203;
  border: 1px solid #212020;
  border-radius: 4px;
  padding: 25px;
}
.error__message {
  color: #f3f3f3;
  font-size: 14px;
  font-weight: 900;
  line-height: 1.6;
}
@media (min-width: 48rem) {
  .error__message {
    font-size: 18px;
  }
}

.input {
  display: flex;
  flex-direction: row;
  position: relative;
  width: 100%;
}
.input__text {
  background: #000000;
  border-radius: 4px;
  padding: 20px 15px 20px 55px;
  font-size: 18px;
  font-weight: 900;
  border: 1px solid #212020;
  outline: none;
  width: 100%;
  color: #f3f3f3;
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
  position: relative;
}
@media (min-width: 48rem) {
  .input__text {
    font-size: 24px;
    min-width: 540px;
    padding: 20px 25px 20px 75px;
  }
}
.input__asset {
  position: absolute;
  top: 17px;
  left: 15px;
  width: 28px;
  height: 30px;
  z-index: 5;
  opacity: 0.6;
}
@media (min-width: 48rem) {
  .input__asset {
    left: 20px;
    top: 15px;
    width: 35px;
    height: 40px;
  }
}

.invoice {
  background: #000000;
  border-radius: 4px;
  width: 100%;
  margin: 0 auto;
  padding: 10px 0;
  opacity: 0;
  visibility: hidden;
  border: 1px solid #212020;
  margin-bottom: 20px;
}
.invoice--opened {
  opacity: 1;
  visibility: visible;
}
.invoice__item {
  padding: 10px 20px;
  display: flex;
  flex-direction: column;
}
@media (min-width: 48rem) {
  .invoice__item {
    flex-direction: row;
  }
}
.invoice__item--nested {
  flex-direction: column;
}
.invoice__item-title {
  color: #838383;
  font-weight: 900;
  min-width: 200px;
  padding-bottom: 5px;
}
@media (min-width: 48rem) {
  .invoice__item-title {
    padding-bottom: 0;
  }
}
.invoice__item-value {
  font-weight: 900;
  color: #f3f3f3;
  width: 100%;
  word-break: break-all;
}
.invoice__item-value--nested {
  padding: 10px 0;
}
.invoice__item-value--nested:last-child {
  padding-bottom: 0;
}
.invoice__nested-item {
  padding: 5px 0;
  display: flex;
  flex-direction: row;
  padding-left: 20px;
}
.invoice__nested-title {
  font-size: 13px;
  color: #838383;
  font-weight: 900;
  min-width: 180px;
}
.invoice__nested-value {
  font-weight: 900;
  color: #f3f3f3;
  width: 100%;
}

.logo {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  margin-bottom: 25px;
}
@media (min-width: 48rem) {
  .logo {
    align-items: center;
    padding: 40px 0;
    padding-bottom: 40px;
    margin-bottom: 45px;
  }
}
.logo__title {
  color: #f3f3f3;
  font-size: 24px;
  font-weight: 900;
}
@media (min-width: 48rem) {
  .logo__title {
    font-size: 60px;
  }
}
.logo__subtitle {
  text-align: left;
  color: #979797;
  padding-top: 20px;
  font-weight: 900;
  font-size: 14px;
}
@media (min-width: 48rem) {
  .logo__subtitle {
    text-align: center;
    font-size: 24px;
  }
}
.logo__subtitle-small {
  display: block;
  font-size: 12px;
  color: #f3f3f3;
  padding-top: 10px;
  font-weight: 900;
}
@media (min-width: 48rem) {
  .logo__subtitle-small {
    font-size: 16px;
  }
}

.submit {
  height: 72px;
  width: 72px;
  border-radius: 50%;
  background: #000000;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  border: 1px solid #212020;
  cursor: pointer;
  z-index: 3;
}
@media (min-width: 64rem) {
  .submit {
    border-radius: 50%;
  }
}
.submit:hover .submit__asset {
  opacity: 1;
}
.submit__asset {
  width: 60%;
  opacity: 0.6;
}
.submit__close {
  border-radius: unset;
  border: 0;
  height: 40px;
  width: 40px;
  top: 10px;
}
@media (min-width: 48rem) {
  .submit__close {
    margin-top: 4px;
    border-radius: 50%;
    margin-right: 1px;
  }
}

.options {
  position: absolute;
  bottom: 20px;
  right: 20px;
  left: 20px;
}
@media (min-width: 48rem) {
  .options {
    top: 20px;
    right: 20px;
    bottom: auto;
    left: auto;
  }
}
.options--hide {
  opacity: 0;
  visibility: hidden;
}
@media (min-width: 48rem) {
  .options--hide {
    opacity: 1;
    visibility: visible;
  }
}
.options__wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
@media (min-width: 48rem) {
  .options__wrapper {
    justify-content: flex-end;
  }
}
.options__bitcoin {
  display: flex;
  flex-direction: row;
  align-items: center;
  order: 2;
}
@media (min-width: 48rem) {
  .options__bitcoin {
    order: 1;
  }
}
.options__bitcoin--opened .options__bitcoin-address {
  opacity: 1;
  visibility: visible;
}
.options__bitcoin--opened .options__bitcoin-icon {
  opacity: 1;
}
.options__bitcoin-address {
  position: absolute;
  right: -5px;
  top: -30px;
  opacity: 0;
  visibility: hidden;
  margin-right: 10px;
  color: #ffffff;
  font-weight: 900;
  font-size: 12px;
}
@media (min-width: 48rem) {
  .options__bitcoin-address {
    position: relative;
    top: auto;
    right: auto;
    padding-right: 10px;
    font-size: 14px;
  }
}
.options__bitcoin-icon-wrapper {
  margin: 0;
  padding: 0;
  line-height: 1;
  appearance: none;
  border: none;
  background: none;
  font-family: inherit;
  outline: 0;
  cursor: pointer;
  width: 40px;
  height: 40px;
  background: #000000;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.options__bitcoin-icon-wrapper[disabled] {
  opacity: 0.5;
}
.options__bitcoin-icon-wrapper:hover .options__bitcoin-icon {
  opacity: 1;
}
.options__bitcoin-icon {
  width: 25px;
  height: 25px;
  opacity: 0.6;
}
.options__github {
  width: 40px;
  height: 40px;
  background: #000000;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  order: 1;
}
@media (min-width: 48rem) {
  .options__github {
    order: 2;
    margin-left: 10px;
  }
}
.options__github:hover .options__github-icon {
  opacity: 1;
}
.options__github-icon {
  width: 25px;
  height: 25px;
  opacity: 0.6;
}

.qrcode {
  width: 72px;
  height: 72px;
  z-index: 10;
  align-self: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-left: 10px;
}
.qrcode:hover .qrcode__img {
  filter: brightness(150%);
}
.qrcode__container {
  position: relative;
  height: 72px;
  width: 72px;
  border-radius: 50%;
  background: #000000;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  border: 1px solid #212020;
  cursor: pointer;
}
.qrcode__container--opened {
  width: 300px;
  height: 300px;
}
.qrcode__modal {
  position: fixed;
  height: 100vh;
  width: 100vw;
  top: 0;
  left: 0;
  background-color: rgba(41, 41, 41, 0.9);
}
.qrcode--opened {
  position: absolute;
  height: 100vh;
  width: 100%;
}
.qrcode__img {
  width: 30px;
  height: 30px;
  background-color: #979797;
  border-radius: 2px;
  box-shadow: 0px 2px 2px 0px #838383;
}
.qrcode__img:hover {
  cursor: pointer;
}
.qrcode__img--opened {
  box-shadow: none;
  background-color: transparent;
  width: 32px;
  height: 32px;
  position: absolute;
  z-index: 10;
  right: 0;
}